<template>
  <div id="role2Chart3" style="width: 100%; height: 500px"></div>
</template>

<script>
  export default {
    name: "role2Chart3",
    mounted(){
      this.draw();
    },
    methods: {
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let role2Chart3 = this.$echarts.init(document.getElementById('role2Chart3'))
        // 绘制图表
        role2Chart3.setOption({
          "animation": true,
          "animationThreshold": 2000,
          "animationDuration": 1000,
          "animationEasing": "cubicOut",
          "animationDelay": 0,
          "animationDurationUpdate": 300,
          "animationEasingUpdate": "cubicOut",
          "animationDelayUpdate": 0,
          "color": [
            "#eaead6",
            "#0d1b2a",
            "#415a77",
            "#2f4554",
            "#1b263b"
          ],
          "series": [
            {
              "type": "pie",
              "name": "\u5931\u7720\u539f\u56e0",
              "clockwise": true,
              "data": [
                {
                  "name": "\u4eba\u9645\u5173\u7cfb",
                  "value": 5.6
                },
                {
                  "name": "\u5de5\u4f5c\u529b\u4e0d\u4ece\u5fc3",
                  "value": 17.1
                },
                {
                  "name": "\u4e1a\u7ee9\u8003\u6838\u538b\u529b",
                  "value": 19.1
                },
                {
                  "name": "\u804c\u573a\u8ff7\u832b",
                  "value": 22.9
                },
                {
                  "name": "\u5de5\u4f5c\u91cf\u5927",
                  "value": 35.3
                }
              ],
              "radius": "55%",
              "center": [
                "50%",
                "50%"
              ],
              "label": {
                "show": true,
                "position": "top",
                "margin": 8
              },
              "tooltip": {
                "show": true,
                "trigger": "item",
                "triggerOn": "mousemove|click",
                "axisPointer": {
                  "type": "line"
                },
                "showContent": true,
                "alwaysShowContent": false,
                "showDelay": 0,
                "hideDelay": 100,
                "formatter": "{a} <br/>{b}: {c}%",
                "textStyle": {
                  "fontSize": 14
                },
                "borderWidth": 0,
                "padding": 5
              },
              "rippleEffect": {
                "show": true,
                "brushType": "stroke",
                "scale": 2.5,
                "period": 4
              }
            }
          ],
          "legend": [
            {
              "data": [
                "\u4eba\u9645\u5173\u7cfb",
                "\u5de5\u4f5c\u529b\u4e0d\u4ece\u5fc3",
                "\u4e1a\u7ee9\u8003\u6838\u538b\u529b",
                "\u804c\u573a\u8ff7\u832b",
                "\u5de5\u4f5c\u91cf\u5927"
              ],
              "selected": {},
              "show": false,
              "padding": 5,
              "itemGap": 10,
              "itemWidth": 25,
              "itemHeight": 14
            }
          ],
          "tooltip": {
            "show": true,
            "trigger": "item",
            "triggerOn": "mousemove|click",
            "axisPointer": {
              "type": "line"
            },
            "showContent": true,
            "alwaysShowContent": false,
            "showDelay": 0,
            "hideDelay": 100,
            "textStyle": {
              "fontSize": 14
            },
            "borderWidth": 0,
            "padding": 5
          }
        });
      }
    }
  }
</script>

<style scoped>

</style>




