                各代际平均入睡时长、<br>睡眠负面影响、<br>睡眠影响因素、<br>“萤火虫族”、<br>网红助眠产品数据：<br>
              </span>
              [1]2019-2020年《喜临门中国睡眠指数报告》<br>

            </p>
          </v-col>
          <v-col style="margin-left: 50px;">
            <p style="text-indent: 0em; text-align: left; font-size: 12px">
              <span>
                睡眠时长及建议睡眠时长数据：<br>
              </span>
              [1]2013年-2020年《喜临门中国睡眠指数报告》<br>
              [2]艾瑞咨询《中国青少年儿童睡眠健康白皮书》<br>
              [3]马冠生,崔朝辉,胡小琪,等.中国居民的睡眠时间分析[J].中国慢性病预防与控制,2006，14(2):68-71<br>
              [4]殷鹏,张梅,李镒冲,等.中国15-69岁居民睡眠状况分析[J].中国慢性病预防与控制,2011,19(3):224-225<br>
              [5]郑棒,林丽玲,余灿清,等.中国成年人睡眠时长、午睡与失眠症状的分布及关联研究[J].中华流行病学杂志,2017,38(4):452-456<br>
              [6]Hirshkowitz, M., Whiton, K., Albert, S. et al. (2015). National Sleep Foundation's sleep time duration recommendations: methodology and results summary. Sleep health, 1(1), 40–43.<br>
            </p>
          </v-col>
          <v-col style="margin-left: 50px; margin-right: 50px">
            <p style="text-indent: 0em; text-align: left; font-size: 12px">
              <span>
                全球各地睡眠满意度及影响度、睡眠呼吸暂停数据：<br>
              </span>
              [1]Philips《2019全球睡眠调查》<br>
              [2]Philips《2020全球睡眠满意度趋势》<br>
              <br>
              <span>
                其他信息来源：<br>
              </span>
              [1]健康中国行动推进委员会《健康中国行动（2019—2030年）》<br>
              [2]李燕燕《拯救睡眠》<br>
              [3]新华网<br>
