    </div>
  </div>
</template>

<script>
  import chart1 from "./chart1";
  import chart2 from "./chart2";
  import Chart3 from "./chart3";
  import Chart4 from "./chart4";
  import Chart5 from "./chart5";
export default {
  name: 'HelloWorld',
  data () {
    return {
      imgRole1: require('../assets/image/role1-1.png'),
      imgRole2: require('../assets/image/role2-1.png'),
      imgRole3: require('../assets/image/role3-1.png'),
      imgRole4: require('../assets/image/role4-1.png'),
      graph1: require('../assets/image/gragh1.png'),
      graph2: require('../assets/image/graph2.png'),
      graph3: require('../assets/image/graph3.png'),
      graph4: require('../assets/image/graph4.png'),
      graph5: require('../assets/image/graph5.png')
    }
  },
  components: {
    Chart3,
    chart1,
    chart2,
    Chart4,
    Chart5
  },
  methods: {
    jump(indexOfRole){
      this.$router.push('/role' + indexOfRole)
    },
    changeImg(indexOfRole){
      switch (indexOfRole) {
        case 1:
          this.imgRole1 = require('../assets/image/role1-2.png');
          break
        case 2:
          this.imgRole2 = require('../assets/image/role2-2.png');
          break
        case 3:
          this.imgRole3 = require('../assets/image/role3-2.png');
          break
        case 4:
          this.imgRole4 = require('../assets/image/role4-2.png');
          break
      }
    },
    changeImgBack(indexOfRole){
      switch (indexOfRole) {
        case 1:
          this.imgRole1 = require('../assets/image/role1-1.png');
          break
        case 2:
          this.imgRole2 = require('../assets/image/role2-1.png');
          break
        case 3:
          this.imgRole3 = require('../assets/image/role3-1.png');
          break
        case 4:
          this.imgRole4 = require('../assets/image/role4-1.png');
          break
      }
    }
  }
}
//<img style="width: 100%" src="../assets/image/background_img.jpg">
</script>

