<template>
  <div class="chart4">

     <div class="container">
       <div class="effect effect-b">
         <h1 class="effect__heading">“下午三四点以后” </h1>
         <h2 class="effect__subheading"><br>不要再喝咖啡、 浓茶等</h2>
       </div>

       <div>
         <h2 style="line-height: 50px">
           <span style="font-size: 50px; font-weight: bolder">医生告诉她</span><br>（鼠标移动查看医生的建议）
         </h2>
       </div>

       <div class="effect effect-b">
         <h1 class="effect__heading">“临近睡眠时间” </h1>
         <h2 class="effect__subheading">避免做一些让自己兴奋的、<br>思维活跃的事情 </h2>
       </div>

       <div class="effect effect-b">
         <h1 class="effect__heading">“睡觉前”</h1>
         <h2 class="effect__subheading">不要过饱或饥饿、 <br>饮酒和大量喝水</h2>
       </div>

       <div class="effect effect-b">
         <h1 class="effect__heading">“至少在睡觉前半小时”</h1>
         <h2 class="effect__subheading"><br>关闭手机等电子产品</h2>
       </div>

       <div class="effect effect-b">
         <h1 class="effect__heading">“不要在床上”</h1>
         <h2 class="effect__subheading"><br>看电视、玩手机</h2>
       </div>
     </div>
  </div>
</template>

<script>
export default {
  name: 'chart4'
}
</script>

<style scoped="scoped">
.pl_div2_div2 div{
  font-size: 16px;
}
.container {
  font-family: 'Alibaba-PuHuiTi-Medium';
  width: 1000px;
  height: 500px;
  display: grid;
  align-items: center;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-template-rows: 50% 50%;
  grid-gap: 10px;
}

 .effect {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0.5em;
  cursor: pointer;
}

.effect .effect__heading {
    font-size: 1.5em;
    line-height: 1;
    position: relative;
    z-index: 2;
  }
.effect .effect__heading:after {
      content: '';
      position: absolute;
    }
.effect .effect__subheading {
    font-family: 'Hindi', Avenir, sans-serif;
    font-size: 1.2em;
    line-height: 2;
    letter-spacing: 0.04em;
  }
.effect-b {
  opacity: 0.5;
  transition: opacity 0.3s $easing;
}
.effect-b:hover {
    opacity: 1;
  }
 .effect-b:hover .effect__heading:after {
    opacity: 0.1;
    transform: translate(-50%, -50%) scaleX(1) rotate(0deg);
  }
 .effect-b:hover .effect__subheading {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
 .effect-b .effect__subheading {
    opacity: 0;
    transform: scale(0.8) rotate(25deg);
    transition: opacity 0.5s $easing, transform 0.5s $easing;
  }
 .effect-b .effect__heading:after {
    opacity: 0;
    content: '⎦';
    font-size: 4em;
    line-height: 1;
    top: calc(100% + 50px);
    left: calc(100% + 50px);
    transform: translate(-50%, 50%) scale(0.8);
    transition: opacity 0.5s $easing, transform 0.5s $easing;
  }
</style>



