<template>
  <div class="role1">
    <v-card
      class="mx-auto"
      max-width="344"
      color="#788ba9"
      dark
    >
      <v-img :src="imgRole1"></v-img>
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title>
            <h1>
              21岁 男性大学生
            </h1>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-card>
    <img style="width: 80%" :src="imgRole1_1" @click="changeImg(1)">
    <p>00后、95后、90后形成矛盾型熬夜，学习、玩游戏、追剧是他们的三大“熬夜杀手”。</p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role1-chart1/>
      </v-card-text>
    </v-card>
    <img style="width: 80%" :src="imgRole1_2" @click="changeImg(2)">
    <p style="line-height: 50px">以00后、95后和90后为代表的年轻人睡眠问题越来越突出，他们主动熬夜占比最高。</p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role1-chart2/>
      </v-card-text>
    </v-card>
    <img style="width: 80%" :src="imgRole1_3" @click="changeImg(3)">
    <p>95后、00后大学生求助于睡眠类app来提升睡眠质量。以蜗牛睡眠APP为例：</p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role1-chart3/>
      </v-card-text>
    </v-card>
    <div class="footer" style="margin-top: 100px">
      <v-btn x-large color="#62786b" dark style="margin-top: 50px" @click="jump">
        <h3>
          点击按钮 返回主页
        </h3>
      </v-btn>
    </div>
  </div>
</template>

<script>
  import role1Chart1 from "./role1Chart1";
  import role1Chart2 from "./role1Chart2";
  import role1Chart3 from "./role1Chart3";
    export default {
      name: "role1",
      data: () => ({
        imgRole1: require('../assets/image/role1.png'),
        imgRole1_1: require('../assets/image/role1-1-1.jpg'),
        numRole1_1: 0,
        imgRole1_2: require('../assets/image/role1-2-1.jpg'),
        numRole1_2: 0,
        imgRole1_3: require('../assets/image/role1-3-1.jpg'),
        numRole1_3: 0
      }),
      components: {
        role1Chart1,
        role1Chart2,
        role1Chart3
      },
      methods: {
        jump(){
          this.$router.push('/')
        },
        changeImg(time){
          switch (time) {
            case 1:
              var nowNum = this.numRole1_1 + 1;
              this.numRole1_1 = nowNum % 4;
              this.imgRole1_1 = require('../assets/image/role1-1-' + (this.numRole1_1 + 1) +'.jpg')
              break
            case 2:
              var nowNum = this.numRole1_2 + 1;
              this.numRole1_2 = nowNum % 2;
              this.imgRole1_2 = require('../assets/image/role1-2-' + (this.numRole1_2 + 1) +'.jpg')
              break
            case 3:
              var nowNum = this.numRole1_3 + 1;
              this.numRole1_3 = nowNum % 2;
              this.imgRole1_3 = require('../assets/image/role1-3-' + (this.numRole1_3 + 1) +'.jpg')
              break
          }
        }
      }
    }
</script>

<style scoped>
  .footer{
    background-color: #181623;
    height: 150px;
    line-height: 30px;
  }
</style>



