<template>
  <div id="role3Chart7" style="width: 100%; height: 300px"></div>
</template>

<script>
  export default {
    name: "role3Chart7",
    mounted(){
      this.draw();
    },
    methods: {
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let role3Chart7 = this.$echarts.init(document.getElementById('role3Chart7'))
        // 绘制图表
        role3Chart7.setOption({
          "animation": true,
          "animationThreshold": 2000,
          "animationDuration": 1000,
          "animationEasing": "cubicOut",
          "animationDelay": 0,
          "animationDurationUpdate": 300,
          "animationEasingUpdate": "cubicOut",
          "animationDelayUpdate": 0,
          "color": [
            "#415a77",
            "#1b263b",
            "#eaead6",
            "#0d1b2a",
            "#778ba7"
          ],
          "series": [
            {
              "type": "bar",
              "legendHoverLink": true,
              "data": [
                33.1,
                29.6,
                27.0,
                25.8,
                25.8,
                25.9,
                26.0
              ],
              "showBackground": false,
              "barMinHeight": 0,
              "barCategoryGap": "20%",
              "barGap": "30%",
              "large": false,
              "largeThreshold": 400,
              "seriesLayoutBy": "column",
              "datasetIndex": 0,
              "clip": true,
              "zlevel": 0,
              "z": 2,
              "label": {
                "show": true,
                "position": "top",
                "margin": 8,
                "formatter": "{c}%"
              },
              "tooltip": {
                "show": true,
                "trigger": "item",
                "triggerOn": "mousemove|click",
                "axisPointer": {
                  "type": "line"
                },
                "showContent": true,
                "alwaysShowContent": false,
                "showDelay": 0,
                "hideDelay": 100,
                "formatter": "{b}<br/>{c}%",
                "textStyle": {
                  "fontSize": 14
                },
                "borderWidth": 0,
                "padding": 5
              },
              "rippleEffect": {
                "show": true,
                "brushType": "stroke",
                "scale": 2.5,
                "period": 4
              }
            }
          ],
          "legend": [
            {
              "data": [
                ""
              ],
              "selected": {
                "": true
              },
              "show": false,
              "padding": 5,
              "itemGap": 10,
              "itemWidth": 25,
              "itemHeight": 14
            }
          ],
          "tooltip": {
            "show": true,
            "trigger": "item",
            "triggerOn": "mousemove|click",
            "axisPointer": {
              "type": "line"
            },
            "showContent": true,
            "alwaysShowContent": false,
            "showDelay": 0,
            "hideDelay": 100,
            "textStyle": {
              "fontSize": 14
            },
            "borderWidth": 0,
            "padding": 5
          },
          "xAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              },
              "data": [
                "00\u540e",
                "95\u540e",
                "90\u540e",
                "85\u540e",
                "80\u540e",
                "70\u540e",
                "70\u524d"
              ]
            }
          ],
          "yAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              }
            }
          ],
          "title": [
            {
              "text": "\u5404\u4ee3\u9645\u6df1\u5ea6\u7761\u7720\u65f6\u957f\u5360\u6bd4",
              "left": "center",
              "top": "20",
              "padding": 5,
              "itemGap": 10
            }
          ]
        });
      }
    }
  }
</script>

<style scoped>

</style>





