<template>
  <div class="role4">
    <v-card
      class="mx-auto"
      max-width="344"
      color="#788ba9"
      dark
    >
      <v-img :src="imgRole4"></v-img>
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title>
            <h1>
              62岁 女性退休者
            </h1>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-card>
    <img style="width: 80%" :src="imgRole4_1">
    <p style="line-height: 50px">
      52.3%的50后养成早睡早起的习惯；<br>
      45.89%的50后自然醒, 43.0%的50后睡在甜美区,可谓是好梦一觉到天明。
    </p>
    <img style="width: 80%" :src="imgRole4_2" @click="changeImg(2)">
    <p>
      不同代际进行养生助眠的方式各不相同，70后更多通过运动、保健品和药品助眠。
    </p>
    <img style="width: 80%" :src="imgRole4_3">
    <p>
      无论工作日还是周末，有超六成的50后在21点-23点睡觉。<br>
      50后虽然睡眠时间不是最长的，但是却是睡的最好的一代人。
    </p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role4-chart4/>
      </v-card-text>
    </v-card>
    <div class="footer">
      <v-btn x-large color="#62786b" dark style="margin-top: 50px" @click="jump">
        <h3>
          点击按钮 返回主页
        </h3>
      </v-btn>
    </div>
  </div>
</template>

<script>
  import role4Chart4 from "./role4Chart4";
  export default {
    name: "role4",
    data () {
      return {
        imgRole4: require('../assets/image/role4.png'),
        imgRole4_1: require('../assets/image/role4-1-1.jpg'),
        imgRole4_2: require('../assets/image/role4-2-1.jpg'),
        numRole4_2: 0,
        imgRole4_3: require('../assets/image/role4-3-1.jpg')
      }
    },
    components: {
      role4Chart4
    },
    methods: {
      jump(){
        this.$router.push('/')
      },
      changeImg(time){
        switch (time) {
          case 2:
            var nowNum = this.numRole4_2 + 1;
            this.numRole4_2 = nowNum % 2;
            this.imgRole4_2 = require('../assets/image/role4-2-' + (this.numRole4_2 + 1) +'.jpg')
            break
        }
      }
    }
  }
</script>

<style scoped>
  .footer{
    background-color: #181623;
    height: 150px;
    line-height: 30px;
    margin-top: 100px;
  }
</style>



