<template>
  <div class="role2">
    <v-card
      class="mx-auto"
      max-width="344"
      color="#788ba9"
      dark
    >
      <v-img :src="imgRole2"></v-img>
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title>
            <h1>
              30岁 女性白领
            </h1>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-card>
    <img style="width: 80%" :src="imgRole2_1" @click="changeImg(1)">
    <p style="line-height: 50px">睡7个小时以上人群中，仍有八成表示没有睡够：</p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role2-chart1/>
      </v-card-text>
    </v-card>
    <img style="width: 80%" :src="imgRole2_2" @click="changeImg(2)">
    <img style="width: 80%" :src="imgRole2_3" @click="changeImg(3)">
    <p style="line-height: 50px">
      造成失眠的职场原因主要分为6类：
    </p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role2-chart3/>
      </v-card-text>
    </v-card>
    <img style="width: 80%" :src="imgRole2_4" @click="changeImg(4)">
    <p>
      15到35岁年轻人中，刷朋友圈、短视频、微博、逛淘宝、听有声app成为他们的主要睡前消遣活动。
    </p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role2-chart4/>
      </v-card-text>
    </v-card>
    <div class="footer">
      <v-btn x-large color="#62786b" dark style="margin-top: 50px" @click="jump">
        <h3>
          点击按钮 返回主页
        </h3>
      </v-btn>
    </div>
  </div>
</template>

<script>
  import role2Chart1 from "./role2Chart1";
  import role2Chart3 from "./role2Chart3";
  import role2Chart4 from "./role2Chart4";
  export default {
    name: "role2",
    data () {
      return {
        imgRole2: require('../assets/image/role2.png'),
        imgRole2_1: require('../assets/image/role2-1-1.jpg'),
        numRole2_1: 0,
        imgRole2_2: require('../assets/image/role2-2-1.jpg'),
        numRole2_2: 0,
        imgRole2_3: require('../assets/image/role2-3-1.jpg'),
        numRole2_3: 0,
        imgRole2_4: require('../assets/image/role2-4-1.jpg'),
        numRole2_4: 0
      }
    },
    components: {
      role2Chart1,
      role2Chart3,
      role2Chart4
    },
    methods: {
      jump(){
        this.$router.push('/')
      },
      changeImg(time){
        switch (time) {
          case 1:
            var nowNum = this.numRole2_1 + 1;
            this.numRole2_1 = nowNum % 3;
            this.imgRole2_1 = require('../assets/image/role2-1-' + (this.numRole2_1 + 1) +'.jpg')
            break
          case 2:
            var nowNum = this.numRole2_2 + 1;
            this.numRole2_2 = nowNum % 5;
            this.imgRole2_2 = require('../assets/image/role2-2-' + (this.numRole2_2 + 1) +'.jpg')
            break
          case 3:
            var nowNum = this.numRole2_3 + 1;
            this.numRole2_3 = nowNum % 2;
            this.imgRole2_3 = require('../assets/image/role2-3-' + (this.numRole2_3 + 1) +'.jpg')
            break
          case 4:
            var nowNum = this.numRole2_4 + 1;
            this.numRole2_4 = nowNum % 6;
            this.imgRole2_4 = require('../assets/image/role2-4-' + (this.numRole2_4 + 1) +'.jpg')
            break
        }
      }
    }
  }
</script>

<style scoped>
  .footer{
    background-color: #181623;
    height: 150px;
    line-height: 30px;
    margin-top: 100px;
  }
</style>



