<template>
  <div class="chart1">
    <v-btn color="#9d9dbf" @click="change(1)">
      查看发现原因
    </v-btn>
    <v-btn color="#9d9dbf" @click="change(2)">
      查看是否就医
    </v-btn>
    <v-btn color="#9d9dbf" @click="change(3)">
      查看就医心理
    </v-btn>
    <div id="chart1" style="width: 100%; height: 400px"></div>
  </div>
</template>

<script>
  export default {
    name: "chart1",
    data () {
      return{
        children: [{
          name: '由另一半发现',
          value: 5
        },{
          name: '患者自行发现',
          value: 4
        }]
      }
    },
    mounted(){
      this.draw();
    },
    methods: {
      change(num){
        switch (num) {
          case 1:
            this.children = [{
              name: '由另一半发现',
              value: 5
            },{
              name: '患者自行发现',
              value: 4
            }]
            this.draw()
            break
          case 2:
            this.children = [{
              name: '已开具处方',
              value: 5
            },{
                name: '未开具处方',
                value: 4
            }]
            this.draw()
            break
          case 3:
            this.children = [{
              name: '认为治疗比疾病更糟糕',
              value: 4
            },{
              name: '认为可以不治疗',
              value: 3
            },{
              name: '坦然接受治疗',
              value: 2
            }]
            this.draw()
            break
        }
      },
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let chart1 = this.$echarts.init(document.getElementById('chart1'))
        var data = [
          {
            name: '非患者',
            value: 86
          },
          {
            name: '未知患者',
            value: 5
          },
          {
            name: '已知患者',
            children: this.children
          }
        ];
        // 绘制图表
        chart1.setOption({
          color: [
            "#fffab8",
            "#61a0a8",
            "#2f4554"
          ],
          series: {
            name: '每百人之中的人数',
            type: 'sunburst',
            data: data,
            sort: 'asc',
            radius: [0, '180%'],
            startAngle: 35,
            center: ['35%', '50%'],
            label: {
              rotate: '0',
              formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c} 人 ',
              align: 'right',
              backgroundColor: '#eee',
              borderColor: '#aaa',
              borderWidth: 1,
              borderRadius: 4,
              rich: {
                a: {
                  color: 'black',
                  lineHeight: 22,
                  align: 'center'
                },
                hr: {
                  borderColor: '#aaa',
                  width: '100%',
                  borderWidth: 0.5,
                  height: 0
                },
                b: {
                  color: 'black',
                  fontSize: 16,
                  lineHeight: 33
                },
                c: {
                  color: 'black'
                }
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              },
              label: {
                rotate: '0',
                formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c} 人 ',
                backgroundColor: '#eee',
                borderColor: '#aaa',
                borderWidth: 1,
                borderRadius: 4
              }
            }
          }
        });
      }
    }
  }
</script>

<style scoped>

</style>



