<template>
  <div id="role4Chart4" style="width: 100%; height: 300px"></div>
</template>

<script>
  export default {
    name: "role4Chart4",
    mounted(){
      this.draw();
    },
    methods: {
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let role4Chart4 = this.$echarts.init(document.getElementById('role4Chart4'))
        // 绘制图表
        role4Chart4.setOption({
          "animation": true,
          "animationThreshold": 2000,
          "animationDuration": 1000,
          "animationEasing": "cubicOut",
          "animationDelay": 0,
          "animationDurationUpdate": 300,
          "animationEasingUpdate": "cubicOut",
          "animationDelayUpdate": 0,
          "color": [
            "#eaead6",
            "#0d1b2a",
            "#415a77",
            "#778ba7",
            "#1b263b"
          ],
          "series": [
            {
              "type": "line",
              "name": "\u5de5\u4f5c\u65e5",
              "connectNulls": false,
              "symbolSize": 4,
              "showSymbol": true,
              "smooth": false,
              "clip": true,
              "step": false,
              "data": [
                [
                  "19:00-20:59",
                  18.5
                ],
                [
                  "21:00-22:59",
                  60.0
                ],
                [
                  "23:00-00:59",
                  16.5
                ],
                [
                  "01:00-02:59",
                  4.2
                ],
                [
                  "03:00\u4ee5\u540e",
                  0.8
                ]
              ],
              "hoverAnimation": true,
              "label": {
                "show": true,
                "position": "top",
                "margin": 8,
                "formatter": function (params) {return params.value[1] + '%'}
              },
              "lineStyle": {
                "show": true,
                "width": 1,
                "opacity": 1,
                "curveness": 0,
                "type": "solid"
              },
              "areaStyle": {
                "opacity": 0
              },
              "zlevel": 0,
              "z": 0
            },
            {
              "type": "line",
              "name": "\u5468\u672b",
              "connectNulls": false,
              "symbolSize": 4,
              "showSymbol": true,
              "smooth": false,
              "clip": true,
              "step": false,
              "data": [
                [
                  "19:00-20:59",
                  6.5
                ],
                [
                  "21:00-22:59",
                  62.3
                ],
                [
                  "23:00-00:59",
                  26.5
                ],
                [
                  "01:00-02:59",
                  4.2
                ],
                [
                  "03:00\u4ee5\u540e",
                  0.4
                ]
              ],
              "hoverAnimation": true,
              "label": {
                "show": true,
                "position": "top",
                "margin": 8,
                "formatter": function (params) {return params.value[1] + '%'}
              },
              "lineStyle": {
                "show": true,
                "width": 1,
                "opacity": 1,
                "curveness": 0,
                "type": "solid"
              },
              "areaStyle": {
                "opacity": 0
              },
              "zlevel": 0,
              "z": 0
            }
          ],
          "legend": [
            {
              "data": [
                "\u5de5\u4f5c\u65e5",
                "\u5468\u672b"
              ],
              "selected": {
                "\u5de5\u4f5c\u65e5": true,
                "\u5468\u672b": true
              },
              "show": true,
              "padding": 5,
              "itemGap": 10,
              "itemWidth": 25,
              "itemHeight": 14
            }
          ],
          "tooltip": {
            "show": true,
            "trigger": "axis",
            "triggerOn": "mousemove|click",
            "axisPointer": {
              "type": "line"
            },
            "showContent": true,
            "alwaysShowContent": false,
            "showDelay": 0,
            "hideDelay": 100,
            "textStyle": {
              "fontSize": 14
            },
            "borderWidth": 0,
            "padding": 5
          },
          "xAxis": [
            {
              "type": "category",
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "boundaryGap": false,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              },
              "data": [
                "19:00-20:59",
                "21:00-22:59",
                "23:00-00:59",
                "01:00-02:59",
                "03:00\u4ee5\u540e"
              ]
            }
          ],
          "yAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              }
            }
          ],
          "title": [
            {
              "padding": 5,
              "itemGap": 10
            }
          ]
        });
      }
    }
  }
</script>

<style scoped>

</style>



