<template>
  <div id="role1Chart1" style="width: 100%; height: 300px"></div>
</template>

<script>
    export default {
        name: "role1Chart1",
      mounted(){
        this.draw();
      },
      methods: {
        draw(){
          // 基于准备好的dom，初始化echarts实例
          let role1Chart1 = this.$echarts.init(document.getElementById('role1Chart1'))
          // 绘制图表
          role1Chart1.setOption({
            "animation": true,
            "animationThreshold": 2000,
            "animationDuration": 1000,
            "animationEasing": "cubicOut",
            "animationDelay": 0,
            "animationDurationUpdate": 300,
            "animationEasingUpdate": "cubicOut",
            "animationDelayUpdate": 0,
            "color": [
              "#eaead6",
              "#0d1b2a",
              "#415a77",
              "#778ba7",
              "#1b263b"
            ],
            "series": [
              {
                "type": "bar",
                "name": "00\u540e",
                "legendHoverLink": true,
                "data": [
                  37.1,
                  29.5,
                  27.1
                ],
                "showBackground": false,
                "barMinHeight": 0,
                "barCategoryGap": "20%",
                "barGap": "30%",
                "large": false,
                "largeThreshold": 400,
                "seriesLayoutBy": "column",
                "datasetIndex": 0,
                "clip": true,
                "zlevel": 0,
                "z": 2,
                "label": {
                  "show": true,
                  "position": "top",
                  "margin": 8,
                  "formatter": "{c}%"
                }
              },
              {
                "type": "bar",
                "name": "90\u540e",
                "legendHoverLink": true,
                "data": [
                  14.6,
                  21.2,
                  32.1
                ],
                "showBackground": false,
                "barMinHeight": 0,
                "barCategoryGap": "20%",
                "barGap": "30%",
                "large": false,
                "largeThreshold": 400,
                "seriesLayoutBy": "column",
                "datasetIndex": 0,
                "clip": true,
                "zlevel": 0,
                "z": 2,
                "label": {
                  "show": true,
                  "position": "top",
                  "margin": 8,
                  "formatter": "{c}%"
                }
              },
              {
                "type": "bar",
                "name": "95\u540e",
                "legendHoverLink": true,
                "data": [
                  17.1,
                  27.9,
                  34.8
                ],
                "showBackground": false,
                "barMinHeight": 0,
                "barCategoryGap": "20%",
                "barGap": "30%",
                "large": false,
                "largeThreshold": 400,
                "seriesLayoutBy": "column",
                "datasetIndex": 0,
                "clip": true,
                "zlevel": 0,
                "z": 2,
                "label": {
                  "show": true,
                  "position": "top",
                  "margin": 8,
                  "formatter": "{c}%"
                }
              }
            ],
            "legend": [
              {
                "data": [
                  "00\u540e",
                  "90\u540e",
                  "95\u540e"
                ],
                "selected": {
                  "00\u540e": true,
                  "90\u540e": true,
                  "95\u540e": true
                },
                "show": true,
                "padding": 5,
                "itemGap": 10,
                "itemWidth": 25,
                "itemHeight": 14
              }
            ],
            "tooltip": {
              "show": true,
              "trigger": "item",
              "triggerOn": "mousemove|click",
              "axisPointer": {
                "type": "line"
              },
              "showContent": true,
              "alwaysShowContent": false,
              "showDelay": 0,
              "hideDelay": 100,
              "formatter": "{c}%",
              "textStyle": {
                "fontSize": 14
              },
              "borderWidth": 0,
              "padding": 5
            },
            "xAxis": [
              {
                "show": true,
                "scale": false,
                "nameLocation": "end",
                "nameGap": 15,
                "gridIndex": 0,
                "inverse": false,
                "offset": 0,
                "splitNumber": 5,
                "minInterval": 0,
                "splitLine": {
                  "show": false,
                  "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                  }
                },
                "data": [
                  "\u5b66\u4e60",
                  "\u6253\u6e38\u620f",
                  "\u8ffd\u5267"
                ]
              }
            ],
            "yAxis": [
              {
                "show": true,
                "scale": false,
                "nameLocation": "end",
                "nameGap": 15,
                "gridIndex": 0,
                "inverse": false,
                "offset": 0,
                "splitNumber": 5,
                "minInterval": 0,
                "splitLine": {
                  "show": false,
                  "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                  }
                }
              }
            ],
            "title": [
              {
                "padding": 5,
                "itemGap": 10
              }
            ]
          });
        }
      }
    }
</script>

<style scoped>

</style>



