<template>
  <div id="role3Chart1" style="width: 100%; height: 300px"></div>
</template>

<script>
  export default {
    name: "role3Chart1",
    mounted(){
      this.draw();
    },
    methods: {
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let role3Chart1 = this.$echarts.init(document.getElementById('role3Chart1'))
        // 绘制图表
        role3Chart1.setOption({
          "animation": true,
          "animationThreshold": 2000,
          "animationDuration": 1000,
          "animationEasing": "cubicOut",
          "animationDelay": 0,
          "animationDurationUpdate": 300,
          "animationEasingUpdate": "cubicOut",
          "animationDelayUpdate": 0,
          "color": [
            "#415a77",
            "#1b263b",
            "#eaead6",
            "#0d1b2a",
            "#778ba7"
          ],
          "series": [
            {
              "type": "bar",
              "name": "value",
              "legendHoverLink": true,
              "data": [
                55.8,
                31.8,
                11.5,
                0.9
              ],
              "showBackground": false,
              "barMinHeight": 0,
              "barCategoryGap": "20%",
              "barGap": "30%",
              "large": false,
              "largeThreshold": 400,
              "seriesLayoutBy": "column",
              "datasetIndex": 0,
              "clip": true,
              "zlevel": 0,
              "z": 2,
              "label": {
                "show": true,
                "position": "top",
                "margin": 8,
                "formatter": "{c}%"
              },
              "tooltip": {
                "show": true,
                "trigger": "item",
                "triggerOn": "mousemove|click",
                "axisPointer": {
                  "type": "line"
                },
                "showContent": true,
                "alwaysShowContent": false,
                "showDelay": 0,
                "hideDelay": 100,
                "formatter": "{b}<br/>{c}%",
                "textStyle": {
                  "fontSize": 14
                },
                "borderWidth": 0,
                "padding": 5
              },
              "rippleEffect": {
                "show": true,
                "brushType": "stroke",
                "scale": 2.5,
                "period": 4
              }
            }
          ],
          "legend": [
            {
              "data": [
                "value"
              ],
              "selected": {
                "value": true
              },
              "show": false,
              "padding": 5,
              "itemGap": 10,
              "itemWidth": 25,
              "itemHeight": 14
            }
          ],
          "tooltip": {
            "show": true,
            "trigger": "item",
            "triggerOn": "mousemove|click",
            "axisPointer": {
              "type": "line"
            },
            "showContent": true,
            "alwaysShowContent": false,
            "showDelay": 0,
            "hideDelay": 100,
            "textStyle": {
              "fontSize": 14
            },
            "borderWidth": 0,
            "padding": 5
          },
          "xAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              },
              "data": [
                "5\u5206\u949f\u4ee5\u5185",
                "5-10\u5206\u949f",
                "10-30\u5206\u949f",
                "30\u5206\u949f\u4ee5\u4e0a"
              ]
            }
          ],
          "yAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              }
            }
          ],
          "title": [
            {
              "padding": 5,
              "itemGap": 10
            }
          ]
        });
      }
    }
  }
</script>

<style scoped>

</style>




