import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import role1 from "../components/role1";
import role2 from "../components/role2";
import role3 from "../components/role3";
import role4 from "../components/role4";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/role1',
      name: 'role1',
      component: role1
    },
    {
      path: '/role2',
      name: 'role2',
      component: role2
    },
    {
      path: '/role3',
      name: 'role3',
      component: role3
    },
    {
      path: '/role4',
      name: 'role4',
      component: role4
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



