<template>
  <div id="role2Chart4" style="width: 100%; height: 300px"></div>
</template>

<script>
  export default {
    name: "role2Chart4",
    mounted(){
      this.draw();
    },
    methods: {
      draw(){
        // 基于准备好的dom，初始化echarts实例
        let role2Chart4 = this.$echarts.init(document.getElementById('role2Chart4'))
        // 绘制图表
        role2Chart4.setOption({
          "animation": true,
          "animationThreshold": 2000,
          "animationDuration": 1000,
          "animationEasing": "cubicOut",
          "animationDelay": 0,
          "animationDurationUpdate": 300,
          "animationEasingUpdate": "cubicOut",
          "animationDelayUpdate": 0,
          "color": [
            "#415a77",
            "#1b263b",
            "#eaead6",
            "#0d1b2a",
            "#778ba7"
          ],
          "series": [
            {
              "type": "pictorialBar",
              "symbol": "roundRect",
              "symbolSize": 18,
              "symbolPosition": "center",
              "symbolOffset": [
                0,
                0
              ],
              "symbolRepeat": "fixed",
              "symbolClip": true,
              "data": [
                54.8,
                49.5,
                36.3,
                29.1,
                19.6
              ],
              "barCategoryGap": "20%",
              "label": {
                "show": false,
                "position": "top",
                "margin": 8
              },
              "tooltip": {
                "show": true,
                "trigger": "item",
                "triggerOn": "mousemove|click",
                "axisPointer": {
                  "type": "line"
                },
                "showContent": true,
                "alwaysShowContent": false,
                "showDelay": 0,
                "hideDelay": 100,
                "formatter": "{b}<br/>{c}%",
                "textStyle": {
                  "fontSize": 14
                },
                "borderWidth": 0,
                "padding": 5
              },
              "rippleEffect": {
                "show": true,
                "brushType": "stroke",
                "scale": 2.5,
                "period": 4
              }
            }
          ],
          "legend": [
            {
              "data": [
                ""
              ],
              "selected": {
                "": true
              },
              "show": true,
              "padding": 5,
              "itemGap": 10,
              "itemWidth": 25,
              "itemHeight": 14
            }
          ],
          "tooltip": {
            "show": true,
            "trigger": "item",
            "triggerOn": "mousemove|click",
            "axisPointer": {
              "type": "line"
            },
            "showContent": true,
            "alwaysShowContent": false,
            "showDelay": 0,
            "hideDelay": 100,
            "textStyle": {
              "fontSize": 14
            },
            "borderWidth": 0,
            "padding": 5
          },
          "xAxis": [
            {
              "show": false,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              }
            }
          ],
          "yAxis": [
            {
              "show": true,
              "scale": false,
              "nameLocation": "end",
              "nameGap": 15,
              "gridIndex": 0,
              "axisLine": {
                "show": true,
                "onZero": true,
                "onZeroAxisIndex": 0,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 0,
                  "curveness": 0,
                  "type": "solid"
                }
              },
              "axisTick": {
                "show": false,
                "alignWithLabel": false,
                "inside": false
              },
              "inverse": false,
              "offset": 0,
              "splitNumber": 5,
              "minInterval": 0,
              "splitLine": {
                "show": false,
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
                }
              },
              "data": [
                "\u5237\u4e2a\u670b\u53cb\u5708",
                "\u5237\u4e2a\u77ed\u89c6\u9891",
                "\u5237\u4e2a\u5fae\u535a",
                "\u5237\u4e2a\u6dd8\u5b9d",
                "\u542c\u4e2a\u6709\u58f0app"
              ]
            }
          ],
          "title": [
            {
              "padding": 5,
              "itemGap": 10
            }
          ],
          grid: {
            containLabel: true
          }
        });
      }
    }
  }
</script>

<style scoped>

</style>




