                  [
                    "\u5317\u4eac",
                    956
                  ],
                  [
                    "\u5e7f\u5dde",
                    685
                  ]
                ],
                "hoverAnimation": true,
                "label": {
                  "show": false,
                  "position": "top",
                  "margin": 8
                },
                "lineStyle": {
                  "show": true,
                  "width": 1,
                  "opacity": 1,
                  "curveness": 0,
                  "type": "solid"
