<template>
  <div class="role3">
    <v-card
      class="mx-auto"
      max-width="344"
      color="#788ba9"
      dark
    >
      <v-img :src="imgRole3"></v-img>
      <v-list-item three-line>
        <v-list-item-content>
          <v-list-item-title>
            <h1>
              46岁 男性创业者
            </h1>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-card>
    <img style="width: 80%" :src="imgRole3_1">
    <p style="line-height: 50px">
      70后属于睡醒即起床类型，基本没有赖床习惯。
    </p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role3-chart1/>
      </v-card-text>
    </v-card>

    <img style="width: 80%" :src="imgRole3_2" @click="changeImg(2)">
    <p>
      脊椎越健康，睡眠质量越高
    </p>
    <img style="width: 80%" :src="imgRole3_3">
    <img style="width: 80%" :src="imgRole3_4" @click="changeImg(4)">
    <p style="line-height: 50px">
      70后最为普遍的夜间活动是辅导孩子写作业。<br>不同代际的夜间活动各异：00后最爱宅在家里，90后最爱k歌，<br>80后加班，60后打牌/打麻将，50后跳广场舞。
    </p>
    <img style="width: 80%" :src="imgRole3_5" @click="changeImg(5)">
    <p>
      在工作日，70、80后平均睡眠时间最少，不足7小时,且睡得不深。
    </p>
    <v-card
      class="mx-auto"
      max-width="40%"
      color="#788ba9"
      dark
    >
      <v-card-text>
        <role3-chart7/>
      </v-card-text>
    </v-card>
    <div class="footer" style="margin-top: 100px">
      <v-btn x-large color="#62786b" dark style="margin-top: 50px" @click="jump">
        <h3>
          点击按钮 返回主页
        </h3>
      </v-btn>
    </div>
  </div>
</template>

<script>
  import role3Chart1 from "./role3Chart1";
  import role3Chart7 from "./role3Chart7";
  export default {
    name: "role3",
    data () {
      return {
        imgRole3: require('../assets/image/role3.png'),
        imgRole3_1: require('../assets/image/role3-1-1.jpg'),
        imgRole3_2: require('../assets/image/role3-2-1.jpg'),
        numRole3_2: 0,
        imgRole3_3: require('../assets/image/role3-3-1.jpg'),
        imgRole3_4: require('../assets/image/role3-4-1.jpg'),
        numRole3_4: 0,
        imgRole3_5: require('../assets/image/role3-5-1.jpg'),
        numRole3_5: 0
      }
    },
    components: {
      role3Chart1,
      role3Chart7
    },
    methods: {
      jump(){
        this.$router.push('/')
      },
      changeImg(time){
        switch (time) {
          case 2:
            var nowNum = this.numRole3_2 + 1;
            this.numRole3_2 = nowNum % 2;
            this.imgRole3_2 = require('../assets/image/role3-2-' + (this.numRole3_2 + 1) +'.jpg')
            break
          case 4:
            var nowNum = this.numRole3_4 + 1;
            this.numRole3_4 = nowNum % 2;
            this.imgRole3_4 = require('../assets/image/role3-4-' + (this.numRole3_4 + 1) +'.jpg')
            break
          case 5:
            var nowNum = this.numRole3_5 + 1;
            this.numRole3_5 = nowNum % 5;
            this.imgRole3_5 = require('../assets/image/role3-5-' + (this.numRole3_5 + 1) +'.jpg')
            break
        }
      }
    }
  }
</script>

<style scoped>
  .footer{
    background-color: #181623;
    height: 150px;
    line-height: 30px;
  }
</style>



