<template>
  <div id="role1Chart2" style="width: 100%; height: 300px"></div>
</template>

<script>
    export default {
        name: "role1Chart2",
      mounted(){
        this.draw();
      },
      methods: {
        draw(){
          // 基于准备好的dom，初始化echarts实例
          let role1Chart2 = this.$echarts.init(document.getElementById('role1Chart2'))
          // 绘制图表
          role1Chart2.setOption({
            "animation": true,
            "animationThreshold": 2000,
            "animationDuration": 1000,
            "animationEasing": "cubicOut",
            "animationDelay": 0,
            "animationDurationUpdate": 300,
            "animationEasingUpdate": "cubicOut",
            "animationDelayUpdate": 0,
            "color": [
              "#eaead6",
              "#0d1b2a",
              "#415a77",
              "#778ba7",
              "#1b263b"
            ],
            "series": [
              {
                "type": "bar",
                "name": "\u4e3b\u52a8\u71ac\u591c",
                "legendHoverLink": true,
                "data": [
                  {
                    "value": 54.8,
                    "percent": 0.548
                  },
                  {
                    "value": 53.5,
                    "percent": 0.535
                  },
                  {
                    "value": 51.3,
                    "percent": 0.513
                  },
                  {
                    "value": 44.1,
                    "percent": 0.441
                  },
                  {
                    "value": 45.5,
                    "percent": 0.455
                  }
                ],
                "showBackground": false,
                "stack": "stack1",
                "barMinHeight": 0,
                "barCategoryGap": "50%",
                "barGap": "30%",
                "large": false,
                "largeThreshold": 400,
                "seriesLayoutBy": "column",
                "datasetIndex": 0,
                "clip": true,
                "zlevel": 0,
                "z": 2,
                "label": {
                  "show": true,
                  "position": "right",
                  "margin": 8,
                  "formatter": function(x){return Number(x.data.percent * 100).toFixed() + '%';}
                },
                "rippleEffect": {
                  "show": true,
                  "brushType": "stroke",
                  "scale": 2.5,
                  "period": 4
                }
              },
              {
                "type": "bar",
                "name": "\u88ab\u52a8\u71ac\u591c",
                "legendHoverLink": true,
                "data": [
                  {
                    "value": 45.2,
                    "percent": 0.452
                  },
                  {
                    "value": 46.7,
                    "percent": 0.467
                  },
                  {
                    "value": 48.7,
                    "percent": 0.487
                  },
                  {
                    "value": 55.9,
                    "percent": 0.559
                  },
                  {
                    "value": 54.5,
                    "percent": 0.545
                  }
                ],
                "showBackground": false,
                "stack": "stack1",
                "barMinHeight": 0,
                "barCategoryGap": "50%",
                "barGap": "30%",
                "large": false,
                "largeThreshold": 400,
                "seriesLayoutBy": "column",
                "datasetIndex": 0,
                "clip": true,
                "zlevel": 0,
                "z": 2,
                "label": {
                  "show": true,
                  "position": "right",
                  "margin": 8,
                  "formatter": function(x){return Number(x.data.percent * 100).toFixed() + '%';}
                },
                "rippleEffect": {
                  "show": true,
                  "brushType": "stroke",
                  "scale": 2.5,
                  "period": 4
                }
              }
            ],
            "legend": [
              {
                "data": [
                  "\u4e3b\u52a8\u71ac\u591c",
                  "\u88ab\u52a8\u71ac\u591c"
                ],
                "selected": {
                  "\u4e3b\u52a8\u71ac\u591c": true,
                  "\u88ab\u52a8\u71ac\u591c": true
                }
              }
            ],
            "tooltip": {
              "show": true,
              "trigger": "item",
              "triggerOn": "mousemove|click",
              "axisPointer": {
                "type": "line"
              },
              "showContent": true,
              "alwaysShowContent": false,
              "showDelay": 0,
              "hideDelay": 100,
              "textStyle": {
                "fontSize": 14
              },
              "borderWidth": 0,
              "padding": 5
            },
            "xAxis": [
              {
                "show": true,
                "scale": false,
                "nameLocation": "end",
                "nameGap": 15,
                "gridIndex": 0,
                "inverse": false,
                "offset": 0,
                "splitNumber": 5,
                "minInterval": 0,
                "splitLine": {
                  "show": false,
                  "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                  }
                },
                "data": [
                  "00\u540e",
                  "95\u540e",
                  "90\u540e",
                  "80\u540e",
                  "70\u540e"
                ]
              }
            ],
            "yAxis": [
              {
                "show": true,
                "scale": false,
                "nameLocation": "end",
                "nameGap": 15,
                "gridIndex": 0,
                "inverse": false,
                "offset": 0,
                "splitNumber": 5,
                "minInterval": 0,
                "splitLine": {
                  "show": false,
                  "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                  }
                }
              }
            ]
          });
        }
      }
    }
</script>

<style scoped>

</style>



